.btn {
  font-size: 1rem;
  font-weight: 700;
  padding: 1rem 2.5rem;
  border-radius: 2rem;
  line-height: 100%;
  cursor: pointer;
  transition: all 0.4s ease-in;
}
.btn__primary {
  border: 2px solid #E4C1AE;
  background: #E4C1AE;
  color: #ffffff;
}
.btn__primary:hover {
  background: #ffffff;
  color: #E4C1AE;
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  background: #ffffff;
}

.container {
  max-width: 1440px;
  width: 100%;
}

h2 {
  line-height: 120%;
}

p {
  line-height: 150%;
}